body {
    font-family: 'Nanum Gothic';
}
p {line-height: 1.6em;}
main {
    padding: 2rem;
    /* min-height: calc(100vh - 150px); */
    height: auto;
    overflow: hidden;  /* 넘치는 콘텐츠를 숨김 */
    margin-bottom: 50px;
  }
nav.navbar {
    background-color: #004c98;
    padding: 1rem;
}

nav.navbar ul.nav-links {
    list-style: none;
    display: flex;
    justify-content: center;
}
nav.navbar ul.nav-links li {
    margin: 0 1rem;
}

/* Link to는,  a로*/

nav.navbar ul.nav-links li a {
    text-decoration: none;
    color: #fff;
    font-size: 1.2rem;
    transition: 0.3s;
}

nav.navbar ul.nav-links li a:hover {
    color: skyblue;
}

.footer {
    background-color: #004c98;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
}